ow: hidden;\r\n}\r\n\r\n.app-bar .app-bar__navigation.open {\r\n  left: 0;\r\n}\r\n\r\n.app-bar .app-bar__navigation ul li a {\r\n  display: inline-block;\r\n  text-decoration: none;\r\n  color: #2c3e50;\r\n  padding: 15px;\r\n  margin-bottom: 5px;\r\n  width: 100%;\r\n  font-size: 18px;\r\n  font-weight: 600;\r\n}\r\n\r\n.app-bar .app-bar__navigation ul li a:hover {\r\n  color: #7c7c7c;\r\n}\r\n\r\n\r\n/*\r\n  Main Content\r\n*/\r\n\r\nmain {\r\n  flex: 1;\r\n}\r\n\r\n.content {\r\n  padding: 32px;\r\n  margin: 0 auto;\r\n  min-height: 100%;\r\n}\r\n\r\n .content__heading {\r\n  font-weight: normal;\r\n  text-align: center;\r\n  color: #2c3e50;\r\n  margin: 20px 0 20px 0;\r\n}\r\n\r\n/*\r\n  * Restaurants\r\n*/\r\n\r\n.restaurants {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 16px;\r\n  margin: 32px 0;\r\n}\r\n\r\n/*\r\n * Restaurant Item Styling\r\n*/\r\n\r\n.restaurant-item {\r\n  width: 100%;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n}\r\n\r\n.restaurant-item__header {\r\n  position: relative;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__thumbnail {\r\n  width: 100%;\r\n  height: 10em;\r\n  object-fit: cover;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating {\r\n  position: absolute;\r\n  padding: 8px;\r\n  bottom: 75%;\r\n  left: 0;\r\n  display: inline-block;\r\n  background-color: #2c3e50;\r\n  color: white;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {\r\n  margin-left: 10px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__city {\r\n  position: absolute;\r\n  padding: 8px;\r\n  bottom: 5px;\r\n  left: 0;\r\n  display: inline-block;\r\n  background-color: #eeeeee;\r\n  color: #2c3e50;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__city .restaurant-item__header__city_